import { getVideoNav, getVideoGroup } from '@/api/video.js'
export default {
	data() {
		return {
			height: "400px",
			tabIndex: 1,
			swiperCurrent: 1,
			navList: [
				{ name: '推荐', id: 34571, list: [] }
			],
			relatedVideo: []
		};
	},
	onLoad() {
		this.getNavList()
	},
	methods: {
		getNavList() {
			getVideoNav().then(res => {
				res.data.length = 10
				res.data.forEach(item => {
					item.list = []
				})
				this.navList = res.data
				return this.setVideoList()
			}).catch(err => {
				this.$u.toast(err.msg || '请重试')
			})
		},
		setVideoList() {
			return new Promise((resolve, reject) => {
				getVideoGroup({ id: this.navList[this.tabIndex].id }).then(res => {
					this.navList[this.tabIndex].list = res.datas
					resolve(res)
				}).catch(err => {
					reject(err)
				})
			})
		},
		// tabs通知swiper切换
		tabsChange(index) {
			this.swiperCurrent = index;
		},
		// swiper-item左右移动，通知tabs的滑块跟随移动
		transition(e) {
			let { dx } = e.detail;
			this.$refs.uTabs.setDx(dx);
		},
		// 由于swiper的内部机制问题，快速切换swiper不会触发dx的连续变化，需要在结束时重置状态
		// swiper滑动结束，分别设置tabs和swiper的状态
		animationfinish(e) {
			let { current } = e.detail;
			this.$refs.uTabs.setFinishCurrent(current);
			this.tabIndex = current;
			this.current = current;
			this.setVideoList()
		},
		// scroll-view到底部加载更多
		onreachBottom() {

		}
	}
}
